          padding-right: 0.5rem!important;
          padding-left: unset;
        }
      }
      .pe-30 {
        padding-right:  1rem!important;
        [dir="rtl"] & {
          padding-left:  1rem!important;
          padding-right: unset;
        }
      }
    }
    .general-widget {
      .web-card {
        .flex-shrink-0 {
          .user-details {
            &.customers {
              ul {
                li {
                  + li {
                    margin-left: -20%;
                    [dir="rtl"] & {
                      margin-left: unset;
                      margin-right: -20%;
                    }
                  }
                }
              }
            }
          }
          .progress-value {
            .progress {
              width: 120px;
            }
          }
        }
        .comment {
          ul {
            li {
              padding: 6px 10px;
            }
          }
        }
      }
      .budget-card {
        .budget-spent {
          display: none;
        }
      }
      .offline-card {
        display: none;
      }
    }
  }
}
